html, body {
  margin: 0;
  height: 100%;
  background-color: #fafafa;
}

.App {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

img {
  border-radius: 50%;
}

h1 {
  font-family: Cagliostro;
  font-size: 2em;
  font-style: normal;
  font-variant: normal;
  font-weight: 700;
  line-height: 26.4px;
}

p {
  font-family: Cagliostro;
  font-size: 2em;
  font-style: normal;
  font-variant: normal;
  font-weight: 400;
  line-height: 20px;
}

a {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.button {
  background-color: #d9d9d9;
  border-radius:28px;
  border: 1px solid #fff;
  display: inline-block;
  cursor: pointer;
  color: #000;
  font-size: 1.5em;
  padding: 16px 31px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #000000;
}

.button:hover {
  background-color: #bfbfbf;
}

.button:active {
  position: relative;
  top: 1px;
}



